import React, { Component } from 'react';
import {Badge} from "../fp-components/fp-minor"
import {Row, Col, Icon,Upload, Button,} from 'antd'
import {COLORS} from  "../constants"

const fileList = [{
  uid: '-1',
  name: 'xxx.png',
  status: 'done',
  url: 'https://zos.alipayobjects.com/rmsportal/jkjgkEfvpUPVyRjUImniVslZfWPnJuuZ.png',
  thumbUrl: 'https://zos.alipayobjects.com/rmsportal/jkjgkEfvpUPVyRjUImniVslZfWPnJuuZ.png',
}];




class UploadImage extends Component {

  updateSettings = (name,value) => {
    let {index,parentIndex,questionIndex,listItem}=this.props;

    let settings = {...listItem.settings};
    settings[name]=value;
    let questionObj = Object.assign({},this.props.listItem,{settings:settings});
    if(this.props.subGroup===1){
      this.props.updateSubQuestion(questionObj,questionIndex,parentIndex,index);
      return;
    }
    this.props.updateQuestion(questionObj,index);
  }
  handleChange = (info) => {
    let fileList = info.fileList;
    console.log(info)
    // 1. Limit the number of uploaded files
    // Only to show two recent uploaded files, and old ones will be replaced by the new
    //fileList = fileList.slice(-2);

    // 2. Read from response and show file link
    fileList = fileList.map((file) => {
      if (file.response) {
        // Component will show file.url as link
      //  file.url = file.response.url;
      }
      return file;
    });

    // 3. Filter successfully uploaded files according to response from server
    fileList = fileList.filter((file) => {
      if (file.response) {
        //return file.response.status === 'success';
      }
      return true;
    });

  //  this.setState({ fileList });
  let {index,parentIndex,questionIndex,listItem}=this.props;

  let settings = {...listItem.settings};
  settings.imageFileList=fileList;
  let questionObj = Object.assign({},this.props.listItem,{settings:settings});
  if(this.props.subGroup===1){
    this.props.updateSubQuestion(questionObj,questionIndex,parentIndex,index);
    return;
  }
  this.props.updateQuestion(questionObj,index);
  return;
  }
  imageSetings = {
   action: '//jsonplaceholder.typicode.com/posts/',
   listType: 'picture',
   onChange:this.handleChange,

 };
  render() {
    console.log(this.props.listItem.settings.imageFileList,this.handleChange)
    let fileList = [];
    if(this.props.listItem.settings.imageFileList!==undefined)
      fileList=this.props.listItem.settings.imageFileList
    return (
      <Upload {...this.imageSetings} fileList={fileList}>
      <Row className="margin-top-30">
          <Col span={12} style={{textAlign:'left'}}>
              Image
          </Col>
          <Col span={12} style={{textAlign:'right'}}>
            <Button>
              <Icon type="upload" /> Upload
            </Button>
          </Col>
      </Row>
      </Upload>
    );
  }
}

export default UploadImage;
