class Authenticate {

  login(data) {
    localStorage.setItem('user', JSON.stringify(data))
  }

  logout(cb) {
    localStorage.removeItem('user');
  }

  isAuthenticated() {
    let user = JSON.parse(localStorage.getItem('user'))
    if (user.token != null) {
        return true
    }
    return false
  }

  getName(){
    let user = JSON.parse(localStorage.getItem('user'))
    return user.user.companyName
  }

  getToken(){
    let user = JSON.parse(localStorage.getItem('user'))
    return user.token
  }
}

export default new Authenticate()
