import React,{Component} from "react"
import FormComponent from "./FormComponent"
import {API_URL} from "../constants.js"
import {connect} from 'react-redux';
import {updateSignupParams} from '../actions/signupLoginAction'
class MainContent extends Component {



  handleChange = (event) => {
    const {name,value} = event.target
    this.props.updateSignupParams(name,value)
  }

  componentDidMount(){
    this.generateCompanyId()
  }

  generateCompanyId = () => {
    this.props.updateSignupParams("companyId","1009")
  }

  handleSubmit = (event) => {
    event.preventDefault();
    //someone must create a function to return formdata object from props/state
    var formBody = new FormData()
    formBody.set("firstName", this.props.firstName)
    formBody.set("lastName", this.props.lastName)
    formBody.set("email", this.props.email)
    formBody.set("password", this.props.password)
    formBody.set("contact", this.props.contact)
    formBody.set("companyName", this.props.companyName)
    formBody.set("companyId", this.props.companyId)
    fetch(API_URL+'/company/signup', {
      method: 'POST',
      body: new URLSearchParams(formBody),
      headers: {
         'Content-Type': 'application/x-www-form-urlencoded'
      }
    })
    .then((response) => response.json())
    .then(data => console.log(data))
  }

  render(){
    return(
      <FormComponent
        handleChange = {this.handleChange}
        handleSubmit = {this.handleSubmit}
        {...this.props}
      />
    )
  }

}
const mapDispatchToProps = {
  updateSignupParams
}

export default connect(null,mapDispatchToProps)(MainContent)
