import React, { Component } from 'react';
import {Layout,Row, Col,Input,Icon} from 'antd'
import CopyDelete from '../images/copy.png'
import Delete from '../images/delete.png'
import QuestionItem from './questionItem'
import {connect} from 'react-redux';
import {updateCurrentIndex,updateQuestions} from '../actions/createAction';
import { DragDropContext, Droppable, Draggable } from "react-beautiful-dnd";

const reorder = (list, startIndex, endIndex) => {
  const result = Array.from(list);
  const [removed] = result.splice(startIndex, 1);
  result.splice(endIndex, 0, removed);

  return result;
};


class QuestionList extends Component {
  getQuestion = (index) => {
    this.props.updateCurrentIndex(index);
  }
  onDragEnd = (result) => {
    if (!result.destination) {
      return;
    }
    const items = reorder(
     this.props.list,
     result.source.index,
     result.destination.index
   );
   this.props.updateQuestions(items)
  }

  render() {
    return (
      <DragDropContext onDragEnd={this.onDragEnd}>
        <Droppable droppableId="droppable">
          {(provided, snapshot) => (
            <div
              {...provided.droppableProps}
             ref={provided.innerRef}

            >
            {
              (this.props.list && this.props.list.length>0)?
              (
                this.props.list.map((listItem,i) => (
                  <Draggable key={`drag-${i}`} draggableId={`drag-${i}`} index={i}>
                    {(provided, snapshot) => (
                      <div
                        onClick={()=> this.getQuestion(i)}
                        ref={provided.innerRef}
                       {...provided.draggableProps}
                       {...provided.dragHandleProps}
                       style={{...provided.draggableProps.style}}
                        >
                        <Row className="create-question-item-container-box">
                          <QuestionItem listItem={listItem} index={i}  />
                        </Row>
                      </div>
                    )}

                  </Draggable>
                ))
              ):null
            }
            </div>
          )}

        </Droppable>
    </DragDropContext>
    );
  }
}
const mapDispatchToProps = {
  updateCurrentIndex,
  updateQuestions
}
export default connect(null,mapDispatchToProps)(QuestionList);
