import React, { Component } from 'react';
import {Layout,Row, Col,Input,Icon,Switch,Select,Upload,message,Carousel,Button} from 'antd'
import CopyDelete from '../../images/copy.png'
import Delete from '../../images/delete.png'
import {COLORS} from  "../../constants"
import {FpSelectMenu,Badge} from "../../fp-components/fp-minor"
import {updateQuestion,copyQuestion,deleteQuestion} from '../../actions/createAction';
import {connect} from 'react-redux'


const Option = Select.Option;
const Dragger = Upload.Dragger;
class PreviewFile extends Component {
  updatePreview = (name,value) => {
    let {index,listItem}=this.props;
    let preview = {...listItem.preview};
    preview[name]=value;
    let questionObj = Object.assign({},this.props.listItem,{preview:preview});
    this.props.updateQuestion(questionObj,index);
  }
  handleChange = (info) => {
    console.log(info)
    const status = info.file.status;
      this.updatePreview('imageFileList',info.fileList)

    if (status !== 'uploading') {
      console.log(info.file, info.fileList);
    }
    else{

    }
    if (status === 'done') {
      //message.success(`${info.file.name} file uploaded successfully.`);
      console.log(info.fileList);
    //  this.updatePreview('imageFileList',info.fileList)
    } else if (status === 'error') {
      message.error(`${info.file.name} file upload failed.`);
    }
  }
  imageSetings = {
   action: '//jsonplaceholder.typicode.com/posts/',
   onChange:this.handleChange,

 };
  inputProps = {
  name: 'file',
  multiple: true,

  action: '//jsonplaceholder.typicode.com/posts/',
  onChange:this.handleChange,
};

  render() {

    let {preview,settings,option} = this.props.listItem;
    let {listItem,index} = this.props;
    let backgroundColor = COLORS.APPCOLOR;
    let fileList = [];
    if(this.props.listItem.preview.imageFileList!==undefined)
      fileList=this.props.listItem.preview.imageFileList
    return(
      <Row className="preview-body" style={{background:backgroundColor}}>
        {
          (settings.imageFileList.length!==0) &&
          (
            <Row >
              <Carousel >
              {
                (settings.imageFileList.length!==0) &&
                settings.imageFileList.map((imagel) =>
                  <div style={{textAlign:'center'}}><img src={imagel.thumbUrl} style={{maxHeight:'160px'}} /></div>
              )
              }
              </Carousel>
            </Row>
          )
        }

          <div style={{marginTop:(settings.imageFileList.length!==0)?"60px":'55%',fontSize:25,color:'white',marginLeft:20,marginBottom:10}}>
          {`${parseInt(index)+1}  ${listItem.question}`}
          </div>
          <div style={{padding:20}}>
                {
                  (preview.uploadedFile!==undefined || preview.uploadedFile!==null)?
                  (
                    <Dragger {...this.inputProps} fileList={fileList} style={{backgroundColor:'#25D15F'}} className="create-preview-drag-container">
                      <p className="ant-upload-drag-icon">
                        <Icon type="cloud-upload"  style={{color:'white'}}/>
                      </p>
                      <p className="ant-upload-text preview-upload-text">Choose a file or drag here</p>
                      <p className="ant-upload-hint preview-upload-text">Support for a single or bulk upload. Strictly prohibit from uploading company data or other band files</p>
                      {
                        (settings.size && settings.sizeValue)?
                        <p className="ant-upload-hint preview-upload-text">Size Limit : {`${settings.sizeValue} MB`}</p>
                        :
                        null
                      }
                    </Dragger>
                  )
                  :
                  (
                    <div>
                      <img src={preview.uploadedFile} />
                      <Button>Cancel</Button>
                    </div>
                  )
                }
                <div style={{padding:20}}>

                    {
                      (settings.description) &&
                      <Row>
                        <div style={{fontSize:14,color:'white',marginLeft:20,marginBottom:10}}>
                        {option}
                        </div>
                      </Row>
                    }

                </div>

          </div>
      </Row>
    );
  }
}
const mapDispatchToProps = {
    updateQuestion,
    copyQuestion,
    deleteQuestion
}
export default connect(null,mapDispatchToProps)(PreviewFile);
