import {UPDATE_SIGNUP_PARAMS,UPDATE_LOGIN_PARAMS} from './types'


export const updateSignupParams = (name,value) => dispatch => {
  console.log("dispatch called")
    dispatch({
        type: UPDATE_SIGNUP_PARAMS,
        name,
        value
    });
}
export const updateLoginParams = (name,value) => dispatch => {
  console.log("dispatch called")
    dispatch({
        type: UPDATE_LOGIN_PARAMS,
        name,
        value
    });
}
