import React,{Component} from "react"
import FormComponent from "./FormComponent"
import {API_URL} from "../constants.js"
import {connect} from 'react-redux';
import {updateLoginParams} from '../actions/signupLoginAction'
import authenticate from "../Services/Authenticate"

class MainContent extends Component{


  handleChange = (event) => {
    const {name,value} = event.target
    this.props.updateLoginParams(name,value);
  }

  handleSubmit = (event) => {
    event.preventDefault();
    var formBody = new FormData()
    formBody.set("email", this.props.email)
    formBody.set("password", this.props.password)
    authenticate.login(formBody)
    fetch(API_URL+'/company/signin', {
      method: 'POST',
      body: new URLSearchParams(formBody)
    })
    .then((response) => response.json())
    .then(data => {
      if(data.token != null){
          authenticate.login(data)
          if(authenticate.isAuthenticated()){
            this.props.history.push("/dashboard");
          }
      }else{
        this.props.updateLoginParams('error',"Error in Login Details")

      }
    })
  }

  render(){
    return(
      <FormComponent
        handleChange = {this.handleChange}
        handleSubmit = {this.handleSubmit}
        {...this.props}
      />
    )
  }
}
const mapDispatchToProps = {
  updateLoginParams
}

export default connect(null,mapDispatchToProps)(MainContent)
