import React, { Component } from 'react';
import {Layout,Row, Col,Input,Icon,Switch,Select,Carousel,Button} from 'antd'
import CopyDelete from '../../images/copy.png'
import Delete from '../../images/delete.png'
import {COLORS} from  "../../constants"
import {FpSelectMenu,Badge} from "../../fp-components/fp-minor"
import Signature from '../../images/signature-preview.svg'
import SignatureCanvas from 'react-signature-canvas'
import {connect} from 'react-redux'
import {updateQuestion,copyQuestion,deleteQuestion} from '../../actions/createAction';
const Option = Select.Option;
class PreviewSign extends Component {
  updatePreview = (name,value) => {
    let {index,listItem}=this.props;
    let preview = {...listItem.preview};
    preview[name]=value;
    let questionObj = Object.assign({},this.props.listItem,{preview:preview});
    this.props.updateQuestion(questionObj,index);
  }
  componentDidMount = () => {

    //this.signCanvas.fromDataURL(this.props.listItem.preview.sign,{});
    let {preview,settings,option} = this.props.listItem;
      console.log(preview.signBase,"mount")
    if(preview.signBase!==undefined && preview.signBase!==null)
      this.sigCanvas.fromDataURL(preview.signBase,{width:300,height:200});
    else
        this.sigCanvas.clear();
  }
  componentDidUpdate = () => {

    //this.signCanvas.fromDataURL(this.props.listItem.preview.sign,{});
    let {preview,settings,option} = this.props.listItem;
    console.log(preview.signBase,"Update")
    if(preview.signBase!==undefined && preview.signBase!==null)
      this.sigCanvas.fromDataURL(preview.signBase,{width:300,height:200});
    else
        this.sigCanvas.clear();
  }
  handleChange = () => {
    let dataUrl = this.sigCanvas.toDataURL('base64string',{width:300,height:200});
    //console.log(dataUrl)
    this.updatePreview('signBase',dataUrl);
  }
  render() {

    let {preview,settings,option} = this.props.listItem;
    let {listItem,index} = this.props;
    let backgroundColor = COLORS.APPCOLOR;

    return(
      <Row className="preview-body" style={{background:backgroundColor}}>
        {
          (settings.imageFileList.length!==0) &&
          (
            <Row >
              <Carousel >
              {
                (settings.imageFileList.length!==0) &&
                settings.imageFileList.map((imagel) =>
                  <div style={{textAlign:'center'}}><img src={imagel.thumbUrl} style={{maxHeight:'160px'}} /></div>
              )
              }
              </Carousel>
            </Row>
          )
        }
          <div style={{marginTop:(settings.imageFileList.length!==0)?"60px":'55%',fontSize:25,color:'white',marginLeft:20,marginBottom:10}}>
          {`${parseInt(index)+1}  ${listItem.question}`}
          </div>
          <div style={{padding:20,width:'100%',textAlign:'center'}}>

                <SignatureCanvas penColor='green'
                   ref={(ref) => { this.sigCanvas = ref }}
                   onEnd={this.handleChange}
                  canvasProps={{width: 300, height: 200, className: 'sigCanvas',backgroundColor:'white'}} backgroundColor={'white'} />
                <Button  style={{background:'#C0FFD5'}} onClick={()=>this.sigCanvas.clear()}> clear</Button>
          </div>
          <div style={{padding:20}}>

              {
                (settings.description) &&
                <Row>
                  <div style={{fontSize:14,color:'white',marginLeft:20,marginBottom:10}}>
                  {option}
                  </div>
                </Row>
              }

          </div>
      </Row>
    );
  }
}
const mapDispatchToProps = {
    updateQuestion,
    copyQuestion,
    deleteQuestion
}
export default connect(null,mapDispatchToProps) (PreviewSign);
