import {UPDATE_SIGNUP_PARAMS} from '../actions/types';

const initialState = {
  firstName:"",
  lastName:"",
  email:"",
  companyName:"",
  companyId:"",
  password:"",
  confirmPassword:"",
  contact:""
}

export default function(state = initialState, action){
  switch (action.type) {
    case UPDATE_SIGNUP_PARAMS:
      return  Object.assign({},state,{[action.name]:action.value})
    default:
      return  state
  }
}
